import Vue from 'vue';
import Router from 'vue-router';
import welcome from '../components/welcome/welcome.vue'//
import goods from '../components/goods/goods.vue'//
import payPage from '../components/payPage/payPage.vue'//
import paySuccess from '../components/paySuccess/paySuccess.vue'//
import orderInfo from '../components/orderInfo/orderInfo.vue'//
import orderList from '../components/orderList/orderList.vue'//
import orderListAgain from '../components/orderListAgain/orderListAgain.vue'//
import addressList from '../components/addressList/addressList.vue'//
import addressEdit from '../components/addressEdit/addressEdit.vue'//


Vue.use(Router);

const routes = [
  {
    path: '/welcome',
    component: welcome,
    meta: {
      title: ''
    }
  }, {
    path: '/goods',
    component: goods,
    name: "goods",
    meta: {
      keepAlive: true // 
    }
  }, {
    path: '/payPage',
    component: payPage,
    name: "payPage"
  }, {
    path: '/paySuccess',
    component: paySuccess,
    name: "paySuccess"
  }, {
    path: '/orderInfo',
    component: orderInfo,
    name: "orderInfo"
  }, {
    path: '/orderList',
    component: orderList,
    name: "orderList"
  }, {
    path: '/orderListAgain',
    component: orderListAgain,
    name: "orderListAgain"
  }, {
    path: '/addressList',
    component: addressList,
    name: "addressList"
  }, {
    path: '/addressEdit',
    component: addressEdit,
    name: "addressEdit"
  }];

export default new Router({
  linkActiveClass: 'active',
  routes,
  base: '/wxdc/dist/',
  // mode: 'history',
});